body {
  background: #eee;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

#main h1{
  color:CornflowerBlue;
  text-align: center;
}

#main{
  padding: 0px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width:60%;
  height: 100%;
}

#job-list{
  padding: 0;
}

#job-list li{
  display: inline-block;
  margin: 12px;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid black;
  -webkit-box-shadow: 1px 2px 3px rgba(0,0,0,0.3);
          box-shadow: 1px 2px 3px rgba(0,0,0,0.3);
  cursor: pointer;
  color:darkcyan;
}

form{
  background: darkcyan;
  padding: 20px;
  position: fixed;
  right: 0;
  bottom: 0;
  width: 400px;
}

form .field{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap:10px;
}

form label{
  text-align: right;
  padding: 6px;
}

form select, form input{
  margin: 4px;
  padding: 6px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

form button{
  color: white;
  font-size: 2em;
  background: blue;
  border: 0;
  padding: 0 10px;
  border-radius: 2px;
  cursor: pointer;
  position: absolute;
  bottom: 10px;
  left:10px;
}

#job-details{
  position: fixed;
  top:0;
  right: 0;
  width: 40%;
  height: 100%;
  background: lightblue;
  padding: 30px;
  overflow: auto;
  -webkit-box-shadow: -2px -3px 5px rgba(0, 0, 0, 0.3);
          box-shadow: -2px -3px 5px rgba(0, 0, 0, 0.3);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: black;

}




